.how-it-works {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.individuals {
  color: var(--color-white);
}
.how-does-employeex-container {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-13xl);
  line-height: 147.9%;
  font-weight: 600;
  color: var(--color-lightskyblue);
}
.head {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 0;
}
.frame,
.rectangle-4420default-parent {
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}
.rectangle-4420default-parent {
  flex: 1;
  gap: var(--gap-71xl);
}
.frame {
  align-self: stretch;
  z-index: 1;
}
.bg-logo-bluebg {
  position: absolute;
  margin: 0 !important;
  bottom: 0;
  left: 0;
  width: 256px;
  height: 188px;
  object-fit: cover;
  z-index: 2;
}
.how-it-works-section {
  align-self: stretch;
  background: linear-gradient(180deg, #163b8a, #3474e8);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-43xl) var(--padding-4xl) var(--padding-78xl)
    var(--padding-31xl);
  align-items: center;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-66xl);
  text-align: center;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .rectangle-4420default-parent {
    flex-direction: column;
  }
}
