}
.already-have-an-container {
  text-decoration: none;
  align-self: stretch;
  position: relative;
  line-height: 28px;
  display: flex;
  align-items: center;
  height: 34px;
  flex-shrink: 0;
  cursor: pointer;
  color: var(--color-lightslategray);
}
.join-us {
  flex: 1;
  border-radius: var(--br-xs);
  background-color: var(--color-aliceblue);
  box-shadow: 0 12px 16px rgba(105, 161, 255, 0.44);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-27xl) var(--padding-28xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-14xl);
  z-index: 1;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-darkslateblue);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .join-us {
    width: 100%;
    flex: unset;
    align-self: stretch;
  }
}
@media screen and (max-width: 420px) {
  .join-us {
    flex: unset;
    align-self: stretch;
  }
}
