.please-feel-free {
  margin: 0;
}
.please-feel-free-container {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 45px;
  font-weight: 500;
  display: inline-block;
  width: 422.77px;
}
.please-feel-free-to-get-in-tou-wrapper {
  flex: 1;
  position: relative;
  height: 94px;
}
.group-icon4 {
  position: relative;
  width: 27.67px;
  height: 33.63px;
}
.map-marker-1 {
  width: 35.67px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  padding: var(--padding-12xs) var(--padding-9xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.iit-delhi,
.our-location {
  align-self: stretch;
  position: relative;
  line-height: 24px;
}
.our-location {
  font-weight: 600;
}
.iit-delhi {
  font-size: var(--font-size-base);
}
.location1 {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 0 var(--padding-21xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-6xl);
}
.map-marker-1-parent {
  flex: 1;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-12xs);
  align-items: center;
  justify-content: center;
  gap: var(--gap-13xl);
  font-size: var(--font-size-lg);
}
.vector-icon11 {
  position: relative;
  width: 33.69px;
  height: 23.13px;
}
.envelope {
  width: 33.69px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.how-can-we {
  font-size: var(--font-size-lg);
  font-weight: 600;
}
.how-can-we,
.infoemployeexcom {
  align-self: stretch;
  position: relative;
  line-height: 24px;
}
.contact,
.envelope-parent,
.frame1 {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.contact {
  flex: 1;
  flex-direction: column;
  padding: 0 var(--padding-21xl);
  gap: var(--gap-2xs);
}
.envelope-parent,
.frame1 {
  align-self: stretch;
  flex-direction: row;
}
.envelope-parent {
  flex: 1;
  gap: var(--gap-13xl);
  font-size: var(--font-size-base);
}
.frame1 {
  overflow: hidden;
  z-index: 0;
}
.footer-child {
  position: relative;
  width: 1279px;
  height: 2.93px;
  z-index: 1;
}
.asset-1-1 {
  position: relative;
  width: 287px;
  height: 40px;
  object-fit: cover;
}
.employeex-all {
  flex: 1;
