}
.vector-icon12 {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.facebook1,
.instagram1,
.linkedin1,
.twitter1 {
  text-decoration: none;
  position: absolute;
  height: 94.74%;
  width: 12.18%;
  top: 5.26%;
  right: 0;
  bottom: 0;
  left: 87.82%;
}
.facebook1,
.instagram1,
.twitter1 {
  height: 93.41%;
  width: 12.01%;
  right: 31.13%;
  bottom: 1.33%;
  left: 56.85%;
}
.facebook1,
.twitter1 {
  height: 73.68%;
  width: 12.18%;
  top: 10.53%;
  right: 62.1%;
  bottom: 15.79%;
  left: 25.72%;
}
.facebook1 {
  height: 94.74%;
  width: 6.77%;
