  left: 0;
}
.social-links1 {
  position: relative;
  width: 143px;
  height: 18px;
}
.frame2 {
  align-self: stretch;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-35xl) 0 0;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-13xl);
  z-index: 2;
  font-size: var(--font-size-mini);
}
.asset-3-2 {
  position: absolute;
  margin: 0 !important;
  bottom: 9.83px;
  left: 0;
  width: 295px;
  height: 188px;
  object-fit: cover;
  z-index: 3;
}
.footer {
  align-self: stretch;
  background: linear-gradient(180deg, #163b8a, #3474e8);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-16xl) var(--padding-xl);
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: var(--gap-36xl);
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-white);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .envelope-parent,
  .map-marker-1-parent,
  .please-feel-free-to-get-in-tou-wrapper {
    flex: unset;
    align-self: stretch;
  }
  .frame1 {
    flex-direction: column;
  }
  .employeex-all {
    flex: unset;
    align-self: stretch;
  }
  .frame2 {
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .envelope-parent,
  .map-marker-1-parent,
  .please-feel-free-to-get-in-tou-wrapper {
    flex: unset;
    align-self: stretch;
  }
  .frame1 {
    flex-direction: column;
  }
  .employeex-all {
    flex: unset;
    align-self: stretch;
  }
  .frame2 {
    flex-direction: column;
  }
}
