.navbar-logo-icon1 {
  position: relative;
  width: 287px;
  height: 46px;
  object-fit: cover;
}
.logo {
  align-self: stretch;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-lg) var(--padding-33xl) var(--padding-22xl);
  align-items: flex-start;
  justify-content: flex-start;
}
.login1,
.login2 {
  align-self: stretch;
  position: relative;
  line-height: 158.9%;
  font-weight: 500;
}
.login2 {
  font-size: var(--font-size-sm);
}
.linkedin,
.vector-icon {
  position: absolute;
  right: 0;
  bottom: 0;
}
.vector-icon {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.linkedin {
  text-decoration: none;
  height: 94.74%;
  width: 12.18%;
  top: 5.26%;
  left: 87.82%;
}
.facebook:hover,
.instagram:hover,
.linkedin:hover {
  cursor: pointer;
}
.instagram {
  height: 93.41%;
  width: 12.01%;
  top: 5.26%;
  right: 31.13%;
  bottom: 1.33%;
  left: 56.85%;
}
.facebook,
.instagram,
.twitter {
  text-decoration: none;
  position: absolute;
}
.twitter {
  height: 73.68%;
  width: 12.18%;
  top: 10.53%;
  right: 62.1%;
  bottom: 15.79%;
  left: 25.72%;
  cursor: pointer;
}
.facebook {
  height: 94.74%;
  width: 6.77%;
  top: 0;
  right: 93.23%;
  bottom: 5.26%;
  left: 0;
}
.social-links {
  position: relative;
  width: 228px;
  height: 29px;
}
.details {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  padding: var(--padding-31xl) var(--padding-15xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-42xl);
  z-index: 0;
  text-align: left;
  font-size: var(--font-size-xl);
  color: var(--color-darkslateblue);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .logo {
    display: none;
  }
  .details {
    flex: unset;
    align-self: stretch;
  }
}
@media screen and (max-width: 420px) {
  .details {
    flex: unset;
    align-self: stretch;
  }
}
