.bg-logo-icon {
  position: absolute;
  margin: 0 !important;
  top: 615px;
  left: 15px;
  width: 295px;
  height: 188px;
  object-fit: cover;
  z-index: 2;
}
.hero {
  align-self: stretch;
  background: linear-gradient(180deg, #fff, rgba(0, 142, 255, 0));
  overflow: hidden;
  display: flex;
  flex-direction: row;
  padding: 42px 51px 103px 15px;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  gap: 100px;
}
.category {
  color: var(--color-royalblue-100);
}
.explore-by-category-container {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.landingpage,
.middle-section {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.middle-section {
  align-self: stretch;
  background: linear-gradient(
    180deg,
    rgba(0, 142, 255, 0.3),
    rgba(255, 255, 255, 0)
  );
  padding: 39px 0 45px;
  gap: 107px;
}
.landingpage {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  text-align: center;
  font-size: var(--font-size-13xl);
  color: var(--color-darkslateblue);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .hero {
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .hero {
    flex-direction: column;
  }
}
