.navbar-logo-icon {
  position: relative;
  width: 287px;
  height: 46px;
  object-fit: cover;
}
.home {
  text-decoration: none;
  position: relative;
  font-weight: 600;
  color: inherit;
}
.home:hover,
.job-seekers:hover {
  cursor: pointer;
}
.hamburgericon-1 {
  position: relative;
  width: 64px;
  height: 40px;
  object-fit: cover;
  display: none;
}
.job-seekers,
.sign-in,
.sign-up {
  position: relative;
  font-weight: 600;
}
.sign-in,
.sign-up {
  text-decoration: none;
  color: inherit;
}
.home:hover,
.job-seekers:hover,
.sign-in:hover,
.sign-up:hover {
  background: linear-gradient(#fff, #fff), #13357f;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.sign-up {
  text-align: center;
}
.sign-up:hover {
  cursor: pointer;
}
.links,
.menu-links {
  flex: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.links {
  align-self: stretch;
  align-items: center;
}
.menu-links {
  height: 40px;
  padding: 0 var(--padding-18xl) 0 var(--padding-51xl);
  box-sizing: border-box;
  align-items: flex-start;
}
.navbar-comp,
.navigation {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.navbar-comp {
  flex: 1;
  justify-content: space-between;
}
.navigation {
  margin: 0;
  align-self: stretch;
  overflow: hidden;
  padding: var(--padding-mid) 0 var(--padding-mid) var(--padding-10xl);
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--color-darkslateblue);
  font-family: var(--font-montserrat);
}
@media screen and (max-width: 960px) {
  .hamburgericon-1 {
    display: flex;
  }
  .links {
    display: none;
  }
}
